body {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../public/background.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 0;
    margin: 0;
}


.game-board {
    height: 60vh;
    width: 60vw;
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
}

.card {
    position: relative;
    width: 20%;
    height: 20%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;

}



.flip {
    transform: rotateY(180deg)
}

img {
    width: 100%;
    height: 100%;
}

.card-front, .card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: 12px;
}

.card-back {
    transform: rotateY(180deg)
}

.card-front {
    background-color: rgba(0, 0, 0, 0.2);
    color: white;
    display: flex;
    justify-content: center;

}

.game-over {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
}

.game-over-btn {
    background-color: yellow;
    color: rgba(100,100, 100);
    border-radius: 12px;
    padding: 2vh;
    font-size: 1.5rem;
    border: none;

}